import React from 'react';
import { Image, StyleSheet, View } from 'react-native';
import { SCREEN_WIDTH, Text } from 'tradewize-component';
import colors from '../../../theme/colors';
import { pending_aproval } from '../../../assets/images';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { RootState } from '../../../redux/store/rootReducer';
import { GetOnboardingStatusData } from '../../../redux/features/register/types';
import { useSelector } from 'react-redux';

const Review = () => {
  const { t } = useAppTranslation();

  const getOnboardingStatus = useSelector((state: RootState) => state.register.getOnboardingStatus);
  const getOnboardingStatusData: GetOnboardingStatusData = getOnboardingStatus.data;


  return (
    <View style={styles.container}>
      <Image
        source={pending_aproval}
        style={styles.pendingAproval}
        resizeMode="contain"
      />

      <Text style={styles.content}>{getOnboardingStatusData?.onboardingStatus !== 7 ? t('THANK_YOU_FOR_YOUR_APPLICATION') : t('REJECT_APPLICATION_DESC')}</Text>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: colors.white,
    borderTopLeftRadius: SCREEN_WIDTH * 0.12,
    borderTopRightRadius: SCREEN_WIDTH * 0.12,
  },
  pendingAproval: {
    width: SCREEN_WIDTH * 0.7,
    height: SCREEN_WIDTH * 0.7,
    alignSelf: 'center',
    marginTop: 16,
  },
  content: {
    fontSize: 18,
    fontWeight: '600',
    color: colors.black,
    textAlign: 'center',
    lineHeight: 28,
    width: SCREEN_WIDTH - 32,
    alignSelf: 'center',
  },
});

export default Review;
