import React, { useCallback, useEffect, useMemo } from 'react';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import { styles } from './styles';
import colors from '../../theme/colors';
import { NavigationService } from '../../services/NavigationService';
import { View, useWindowDimensions } from 'react-native';
import { TabView, SceneMap } from 'react-native-tab-view';
import Review from './components/Review';
import Interview from './components/Interview';
import TabBar from './components/TabBar';
import { useDispatch, useSelector } from 'react-redux';
import { clearOnboardingStatus, getOnboardingStatusAsync } from '../../redux/features/register/actions';
import { RootState } from '../../redux/store/rootReducer';
import { GetOnboardingStatusData } from '../../redux/features/register/types';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import BackgroundTimer from 'react-native-background-timer';

const renderScene = SceneMap({
  review: () => <Review />,
  interview: () => <Interview />,
});

const routes = [
  { key: 'review', title: 'Review' },
  { key: 'interview', title: 'Interview' },
];

const AccountActivationStatus = ({ route }: { route?: { params: { accountId: string } } }) => {
  const { t } = useAppTranslation();
  const dispatch = useDispatch()
  const { accountId } = route?.params || {};
  const getOnboardingStatus = useSelector((state: RootState) => state.register.getOnboardingStatus);
  const getOnboardingStatusData: GetOnboardingStatusData = getOnboardingStatus.data;

  const layout = useWindowDimensions();
  const [index, setIndex] = React.useState(0);

  const handlePressTab = (i: number) => {
    setIndex(i);
  };

  const handleClearOnboardingStatus = useCallback(() => {
    dispatch(clearOnboardingStatus());
    setTimeout(() => {
      NavigationService.goBack()
    }, 150);
  }, [dispatch])

  useEffect(() => {
    if (accountId) {
      dispatch(getOnboardingStatusAsync(accountId));

      const intervalId = BackgroundTimer.setInterval(() => {
        dispatch(getOnboardingStatusAsync(accountId));
      }, 30000);

      // Clean up the interval when component unmounts
      return () => {
        BackgroundTimer.clearInterval(intervalId);
      };
    }
  }, [accountId, dispatch]);

  useEffect(() => {
    if (getOnboardingStatusData?.interviewStatus === 2) {
      handleClearOnboardingStatus()
    }
  }, [getOnboardingStatusData?.interviewStatus, handleClearOnboardingStatus])


  const getTitleHeader = useMemo(() => {
    if (getOnboardingStatusData?.onboardingStatus <= 7) {
      return 'WAITING_FOR_REVIEW';
    } else if (getOnboardingStatusData?.interviewStatus > 0 && getOnboardingStatusData?.interviewStatus <= 4) {
      return 'SCHEDULE_INTERVIEW';
    } else {
      return 'ACCOUNT';
    }
  }, [getOnboardingStatusData?.onboardingStatus, getOnboardingStatusData?.interviewStatus]);

  return (
    <BaseContainer
      backgroundStatusBarColor={colors.primary}
      backgroundColor={colors.primary}
      backgroundBottomSafeAreaColor={colors.white}
      style={styles.container}
    >
      <HeaderBase
        onPressBack={handleClearOnboardingStatus}
        containerStyle={styles.header}
        styleButtonLeft={styles.styleButtonBack}
        title={t(getTitleHeader)}
        titleStyle={styles.titleHeader}
      />
      <View style={styles.contentContainer}>
        <TabView
          swipeEnabled={false}
          lazy
          navigationState={{ index, routes }}
          renderScene={renderScene}
          onIndexChange={setIndex}
          initialLayout={{ width: layout.width }}
          renderTabBar={props => (
            <TabBar handlePressTab={handlePressTab} {...props} />
          )}
        />
      </View>
    </BaseContainer>
  );
};

export default AccountActivationStatus;
