import React, { useMemo } from 'react';
import { Image, Linking, StyleSheet, View } from 'react-native';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import colors from '../../../theme/colors';
import { useSelector } from 'react-redux';
import { RootState } from '../../../redux/store/rootReducer';
import { GetOnboardingStatusData } from '../../../redux/features/register/types';
import { scheduleInterview, verifyEmail } from '../../../assets/images';
import { useAppTranslation } from '../../../hooks/useAppTranslation';

const Interview = () => {
  const { t } = useAppTranslation();
  const getOnboardingStatus = useSelector((state: RootState) => state.register.getOnboardingStatus);
  const getOnboardingStatusData: GetOnboardingStatusData = getOnboardingStatus.data;

  const getImage = useMemo(() => {
    if (getOnboardingStatusData?.interviewStatus > 0 && getOnboardingStatusData?.interviewStatus < 3) {
      return scheduleInterview;
    }
    if (getOnboardingStatusData?.interviewStatus === 4) {
      return verifyEmail;
    }
  }, [getOnboardingStatusData?.interviewStatus]);

  return (
    <View style={styles.container}>
      <Image
        source={getImage}
        style={styles.imageInterview}
        resizeMode="contain"
      />

      <Text style={styles.titleInterview}>
        {t('SCHEDULE_INTERVIEW')}
      </Text>

      {getOnboardingStatusData?.calendlyLink && (
        <View style={styles.calendlyContainer}>
          <Text style={styles.calendlyText}>
            {t('CLICK_HERE_TO_JOIN_INTERVIEW')}
          </Text>
          <Button
            variant="ghost"
            style={styles.calendlyButton}
            onPress={() => Linking.openURL(getOnboardingStatusData?.calendlyLink)}
          >
            <Text
              style={styles.calendlyButtonText}
            >
              {t('OPEN_CALENDLY')}
            </Text>
          </Button>
        </View>
      )}

    </View>
  );
};



const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: colors.white,
    borderTopLeftRadius: SCREEN_WIDTH * 0.12,
    borderTopRightRadius: SCREEN_WIDTH * 0.12,
  },
  imageInterview: {
    width: SCREEN_WIDTH * 0.7,
    height: SCREEN_WIDTH * 0.7,
    alignSelf: 'center',
    marginTop: 16,
  },
  titleInterview: {
    fontSize: 30,
    fontWeight: 'bold',
    color: colors.black,
    textAlign: 'center',
    marginTop: 16,
    lineHeight: 36,
    letterSpacing: -0.2,
  },
  calendlyContainer: {
    alignItems: 'center',
    marginTop: 16,
  },
  calendlyText: {
    fontSize: 14,
    fontWeight: 'bold',
    color: colors.black,
    textAlign: 'center',
  },
  calendlyButton: {
    minHeight: 0,
    paddingVertical: 0,
    paddingHorizontal: 0,
  },
  calendlyButtonText: {
    color: colors.mainColorPrimary,
    textDecorationLine: 'underline',
    fontSize: 14,
    fontWeight: 'bold',
    lineHeight: 20,
  },
});
export default Interview;
