import React, { useEffect } from "react";
import { useCallback } from "react";
import { Linking, Platform, ScrollView, View } from "react-native";
import DeviceInfo from "react-native-device-info";
import { Text } from "tradewize-component";
import { compareVersions, logger, UPDATE_APP_URL_ANDROID, UPDATE_APP_URL_IOS } from "../utils";
import { useAppTranslation } from "./useAppTranslation";
import { useDispatch, useSelector } from "react-redux";
import { fetchGetVersionAppAsync, hideConfirmModal, showAlert, showConfirmModal } from "../redux/features/app/actions";
import { RootState } from "../redux/store/types";
import { STORAGE_KEY, StorageService } from "../services/StorageService";
import { NavigationService } from "../services/NavigationService";
import { SCREENS_ENUM } from "../types/global";

export const useCheckUpdateApp = () => {
    const { t } = useAppTranslation();
    const dispatch = useDispatch();
    const versionApp = useSelector((state: RootState) => state.app.versionApp);
    const platform = Platform.OS;

    const renderWhatsNewContent = useCallback((versionByPlatform: any) => {
        const latestVersionData = versionByPlatform?.latestVersion;
        if (!latestVersionData) {
            return null;
        }
        // const versionNumber = latestVersionData[platform];
        const newFeatures =
            platform === 'android'
                ? latestVersionData?.newFeaturesAndroid
                : latestVersionData?.newFeaturesIos;
        const fixedFeatures =
            platform === 'android'
                ? latestVersionData?.fixedFeaturesAndroid
                : latestVersionData?.fixedFeaturesIos;

        const parseFeatures = (features: string | null | undefined) => {
            if (!features) return [];
            return features
                .split(/\r\n|\n/)
                .map(line => line.trim())
                .filter(line => line.length > 0 && line !== '•');
        };

        const newFeaturesList = parseFeatures(newFeatures);
        const fixedFeaturesList = parseFeatures(fixedFeatures);

        const children: React.ReactNode[] = [
            React.createElement(
                Text,
                {
                    key: 'version-title',
                    style: { marginBottom: 4, textAlign: 'left' },
                    children: `What's New`,
                },
            ),
        ];

        if (newFeaturesList.length > 0) {
            children.push(
                React.createElement(
                    View,
                    { key: 'new-features-section', style: { marginBottom: 4 } },
                    React.createElement(
                        View,
                        {
                            style: {
                                flexDirection: 'row',
                                alignItems: 'center',
                                marginBottom: 4,
                            },
                        },
                        React.createElement(
                            Text,
                            {
                                style: { marginLeft: 4 },
                                children: '✨ New',
                            }),
                    ),
                    ...newFeaturesList.map((feature: string, index: number) =>
                        React.createElement(
                            View,
                            {
                                key: `new-${index}`,
                                style: {
                                    flexDirection: 'row',
                                    marginBottom: 2,
                                    paddingLeft: 8,
                                },
                            },
                            React.createElement(
                                Text,
                                {
                                    style: { marginRight: 8 },
                                    children: '•',
                                },
                            ),
                            React.createElement(
                                Text,
                                {
                                    style: { flex: 1 },
                                    children: feature.replace(/^•\s*/, '').trim(),
                                },
                                feature.replace(/^•\s*/, '').trim(),
                            ),
                        ),
                    ),
                ),
            );
        }

        if (fixedFeaturesList.length > 0) {
            children.push(
                React.createElement(
                    View,
                    { key: 'fixed-features-section', style: { marginBottom: 4 } },
                    React.createElement(
                        View,
                        {
                            style: {
                                flexDirection: 'row',
                                alignItems: 'center',
                                marginBottom: 4,
                            },
                        },
                        React.createElement(
                            Text,
                            {
                                style: { marginLeft: 4 },
                                children: '✓ Fixed',
                            },
                        ),
                    ),
                    ...fixedFeaturesList.map((feature: string, index: number) =>
                        React.createElement(
                            View,
                            {
                                key: `fixed-${index}`,
                                style: {
                                    flexDirection: 'row',
                                    marginBottom: 2,
                                    paddingLeft: 8,
                                },
                            },
                            React.createElement(
                                Text,
                                {
                                    style: { marginRight: 8 },
                                    children: '•',
                                },
                            ),
                            React.createElement(
                                Text,
                                {
                                    style: { flex: 1 },
                                    children: feature.replace(/^•\s*/, '').trim(),
                                },
                            ),
                        ),
                    ),
                ),
            );
        }

        if (children.length === 1) {
            // Only version title, no features
            return null;
        }

        return React.createElement(View, { style: { marginTop: 4 } }, ...children);
    }, [platform]);

    const checkVersion = useCallback(async () => {
        try {
            const getVersionByApp = versionApp?.data?.find((item: any) => item.appType === 0)?.data;
            const versionByPlatform = getVersionByApp?.[platform];
            const realAppVersion = DeviceInfo.getVersion();
            const latestVersion = versionByPlatform?.latestVersion[platform];
            const minRequiredVersion =
                versionByPlatform?.minRequiredVersion[platform];

            if (getVersionByApp?.isLocal) {
                NavigationService.replace(SCREENS_ENUM.LoginScreen);
                return;
            }

            if (!latestVersion || !minRequiredVersion) {
                logger('Version info missing');
                NavigationService.replace(SCREENS_ENUM.LoginScreen);
                return;
            }

            // Bảo vệ logic sai ở backend
            if (compareVersions(minRequiredVersion, latestVersion) > 0) {
                logger('Invalid version range: minRequiredVersion > latestVersion');
                NavigationService.replace(SCREENS_ENUM.LoginScreen);
                return;
            }

            const localVersion = await StorageService.get(
                STORAGE_KEY.VERSION_APP,
            );
            if (!localVersion) {
                const versionBySave = {
                    [platform]: {
                        realAppVersion,
                    },
                };

                await StorageService.set(
                    STORAGE_KEY.VERSION_APP,
                    versionBySave,
                );
                NavigationService.replace(SCREENS_ENUM.LoginScreen);
                return;
            }

            if (
                compareVersions(
                    localVersion[platform].realAppVersion,
                    realAppVersion,
                ) < 0
            ) {
                const updateVersion = {
                    ...localVersion,
                    [platform]: {
                        ...localVersion[platform],
                        realAppVersion,
                    },
                };

                await StorageService.set(
                    STORAGE_KEY.VERSION_APP,
                    updateVersion,
                );
                NavigationService.replace(SCREENS_ENUM.LoginScreen);
                return;
            }
            const forceUpdate =
                compareVersions(
                    localVersion[platform]?.realAppVersion,
                    minRequiredVersion,
                ) < 0;
            const optionalUpdate =
                compareVersions(
                    localVersion[platform]?.realAppVersion,
                    latestVersion,
                ) < 0 &&
                compareVersions(
                    localVersion[platform]?.realAppVersion,
                    minRequiredVersion,
                ) >= 0;

            if (forceUpdate) {
                const latestVersionData = versionByPlatform?.latestVersion;
                const newFeatures =
                    platform === 'android'
                        ? latestVersionData?.newFeaturesAndroid
                        : latestVersionData?.newFeaturesIos;
                const fixedFeatures =
                    platform === 'android'
                        ? latestVersionData?.fixedFeaturesAndroid
                        : latestVersionData?.fixedFeaturesIos;
                const whatsNewContent = renderWhatsNewContent(versionByPlatform);
                const modalContent = React.createElement(
                    ScrollView,
                    {
                        style: { maxHeight: 200 },
                        showsVerticalScrollIndicator: true,
                        nestedScrollEnabled: true,
                        contentContainerStyle: { paddingBottom: 8, flexGrow: 1 },
                    },
                    React.createElement(
                        Text,
                        {
                            style: {
                                marginBottom: whatsNewContent ? 4 : 0,
                                textAlign: 'center',
                                fontSize: 16,
                                fontWeight: 'bold',
                            },
                            children: t('UPDATE_APP_DESCRIPTION'),
                        },
                    ),
                    whatsNewContent,
                );
                dispatch(
                    showAlert({
                        visible: true,
                        type: 'warning',
                        title: t('NEW_VERSION_AVAILABLE'),
                        customContent: !newFeatures && !fixedFeatures ? null : modalContent,
                        buttonText: t('UPDATE_NOW'),
                        isHideModalOnClose: false,
                        onClose: () => {
                            let linkApp;
                            if (platform === 'ios') {
                                linkApp = UPDATE_APP_URL_IOS;
                            } else {
                                linkApp = UPDATE_APP_URL_ANDROID;
                            }
                            Linking.openURL(linkApp || '');
                        },
                    }),
                );
            } else if (optionalUpdate) {
                const whatsNewContent = renderWhatsNewContent(versionByPlatform);
                const latestVersionData = versionByPlatform?.latestVersion;
                const newFeatures =
                    platform === 'android'
                        ? latestVersionData?.newFeaturesAndroid
                        : latestVersionData?.newFeaturesIos;
                const fixedFeatures =
                    platform === 'android'
                        ? latestVersionData?.fixedFeaturesAndroid
                        : latestVersionData?.fixedFeaturesIos;
                const modalContent = React.createElement(
                    ScrollView,
                    {
                        style: { maxHeight: 200 },
                        showsVerticalScrollIndicator: true,
                        nestedScrollEnabled: true,
                        contentContainerStyle: { paddingBottom: 8, flexGrow: 1 },
                    },
                    React.createElement(
                        Text,
                        {
                            style: {
                                marginBottom: whatsNewContent ? 4 : 0,
                                textAlign: 'center',
                            },
                            children: t('UPDATE_APP_DESCRIPTION'),
                        },
                    ),
                    whatsNewContent,
                );
                dispatch(
                    showConfirmModal({
                        visible: true,
                        title: t('NEW_VERSION_AVAILABLE'),
                        customDescription: !newFeatures && !fixedFeatures ? null : modalContent,
                        confirmText: t('UPDATE_NOW'),
                        cancelText: t('NOT_NOW'),
                        onConfirm: () => {
                            let linkApp;
                            if (platform === 'ios') {
                                linkApp = UPDATE_APP_URL_IOS;
                            } else {
                                linkApp = UPDATE_APP_URL_ANDROID;
                            }
                            Linking.openURL(linkApp || '');
                        },
                        onCancel: () => {
                            dispatch(hideConfirmModal());
                            setTimeout(() => {
                                NavigationService.replace(SCREENS_ENUM.LoginScreen);
                            }, 350);
                        },
                    }),
                );
            } else {
                // Không cần update
                logger('App is up to date.');
                setTimeout(() => {
                    NavigationService.replace(SCREENS_ENUM.LoginScreen);
                }, 350);
            }
        } catch (error) {
            logger('Error check version', error);
            setTimeout(() => {
                NavigationService.replace(SCREENS_ENUM.LoginScreen);
            }, 350);
        }
    }, [versionApp?.data, renderWhatsNewContent, t, dispatch, platform]);

    useEffect(() => {
        checkVersion();
    }, [checkVersion]);

    useEffect(() => {
        dispatch(fetchGetVersionAppAsync());
    }, [dispatch]);
};