import React, { useCallback, useEffect, useState } from 'react';
import { StyleSheet, View } from 'react-native';
import { Text } from 'tradewize-component';
import colors from '../../../theme/colors';
import {
  NavigationState,
  SceneRendererProps,
  TabDescriptor,
} from 'react-native-tab-view';
import { RootState } from '../../../redux/store/rootReducer';
import { useSelector } from 'react-redux';
import { GetOnboardingStatusData } from '../../../redux/features/register/types';

const TabBar = (
  props: SceneRendererProps & {
    handlePressTab: (index: number) => void;
    navigationState: NavigationState<any>;
    options: Record<string, TabDescriptor<any>> | undefined;
  },
) => {
  const getOnboardingStatus = useSelector((state: RootState) => state.register.getOnboardingStatus);
  const getOnboardingStatusData: GetOnboardingStatusData = getOnboardingStatus.data;

  const { navigationState, handlePressTab } = props;
  const [activeIndex, setActiveIndex] = useState(0);

  const backgroundColorTabByIndex = useCallback((i: number) => {
    if (i === 0) {
      switch (getOnboardingStatusData?.onboardingStatus) {
        case 5:
          return colors.mainColorPrimary;
        case 6:
          return colors.alertColorSuccess;
        case 7:
          return colors.alertColorError;
        default:
          return colors.grayScale70;
      }
    } else {
      switch (getOnboardingStatusData?.interviewStatus) {
        case 0:
          return colors.grayScale70;
        case 1:
        case 2:
          return colors.mainColorPrimary;
        case 3:
          return colors.alertColorSuccess;
        case 4:
          return colors.alertColorError;
        default:
          return colors.grayScale70;
      }
    }
  }, [getOnboardingStatusData?.onboardingStatus, getOnboardingStatusData?.interviewStatus]);

  useEffect(() => {
    if (getOnboardingStatusData?.interviewStatus > 0 && getOnboardingStatusData?.interviewStatus <= 4) {
      setActiveIndex(1);
      handlePressTab(1);
    } else {
      setActiveIndex(0);
      handlePressTab(0);
    }
  }, [getOnboardingStatusData?.onboardingStatus, getOnboardingStatusData?.interviewStatus, handlePressTab]);

  return (
    <View style={styles?.tabBar}>
      {navigationState.routes.map((route, i) => {
        return (
          <View key={route.key} style={styles.tabItem}>
            <View
              style={[
                styles.lineTab,
                activeIndex === i && { backgroundColor: colors.mainColorPrimary },
                i !== activeIndex && { backgroundColor: backgroundColorTabByIndex(i) },
              ]}
            />
            <Text
              style={[
                styles.tabItemText,
                activeIndex === i && { ...styles.tabItemTextActive, color: colors.mainColorPrimary },
                i !== activeIndex && { color: backgroundColorTabByIndex(i) },
              ]}
            >
              {route.title}
            </Text>
          </View>
        );
      })}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: colors.red,
  },
  tabBar: {
    flexDirection: 'row',
    paddingHorizontal: 16,
    paddingBottom: 16,
    gap: 2,
  },
  tabItem: {
    flex: 1,
    alignItems: 'center',
  },
  tabItemText: {
    fontSize: 14,
    fontWeight: '400',
    color: colors.black,
    lineHeight: 20,
  },
  lineTab: {
    height: 8,
    borderTopRightRadius: 16,
    borderBottomLeftRadius: 16,
    width: '100%',
    backgroundColor: colors.grayScale70,
  },
  tabItemTextActive: {
    fontWeight: '500',
  },
});

export default TabBar;
